<template>
    <div>
      <head-top>
        <span slot='logo' class="head-logo"></span>
        <div slot='nav' class="nav-bar">
          <a :class="{current: index === 'first-screen-container' ? true : false}" @click="jumpTo('first-screen-container')">首页</a>
          <a :class="{current: index === 'service' ? true : false}" @click="jumpTo('service')">服务</a>
          <a :class="{current: index === 'caseShow' ? true : false}" @click="jumpTo('caseShow')">案例</a>
          <a :class="{current: index === 'products' ? true : false}" @click="jumpTo('products')">产品</a>
          <a :class="{current: index === 'designer' ? true : false}" @click="jumpTo('designer')">团队</a>
          <a :class="{current: index === 'contact' ? true : false}" @click="jumpTo('contact')">联系</a>
        </div>
      </head-top>
      <first-screen></first-screen>
      <service></service>
      <cases></cases>
      <products></products>
      <designer></designer>
      <contact></contact>
    </div>
</template>

<script>
import headTop from '../components/head'
import firstScreen from '../components/firstScreen'
import service from '../components/service'
import cases from '../components/case'
import products from '../components/products'
import designer from '../components/designer'
import contact from '../components/contact'

export default{
    data(){
        return{
            index: 'first-screen-container'
        }
    },
    components:{
        headTop,
        firstScreen,
        service,
        cases,
        products,
        designer,
        contact
    },
    methods:{
        jumpTo: function(str){
            this.index = str;
            var distance = document.getElementById(str).offsetTop;
            document.body.scrollTop = distance;
        }
    }
}
</script>

<style lang="scss" scoped>
  @import '../style/mixin';

  .head-logo{
    left: 18.5%;
    @include wh(40px, 40px);
    @include ct;
    @include bis('../assets/logo.png');
  }
  .nav-bar{
    right: 18.5%;
    @include ct;

    a{
      @include sc(14px, $fc);
      cursor: pointer;
      margin-left: 20px;

      &.current{
        padding-bottom: 4px;
        border-bottom: 2px solid $red;
      }
    }
  }
</style>



