import Vue from 'vue'
import Router from 'vue-router'
import App from '../App'
import Home from '../page/home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: App,
      children: [
        {
          path: '',
          redirect: '/home'
        },
        {
          path: '/home',
          component: Home
        }
      ]
    }
  ]
})



