<template>
    <div id="caseShow">
        <header>
          <div class="title-block">
            <div class="title">
              CASE
              <span class="small-title">案列中心</span>
            </div>
          </div>
        </header>

        <swipe v-model="index" :time="time" class="swipe-container">
          <swipe-item class="swipe-item-class img-src">
            <div class="describe-background"></div>
            <div class="describe">
              <div class="btn">
                <button>办公家具装修案例</button>
              </div>
              <div class="describe-word">
                故事的小黄花<br>
                从出生那年就飘着<br>
                童年的荡秋千<br>
                随记忆一直晃到现在
              </div>
            </div>
          </swipe-item>
          <swipe-item class="back_img img-src">item2</swipe-item>
          <swipe-item class="back_img img-src">item3</swipe-item>
        </swipe>
    </div>
</template>

<script>
    import swipe from './swipe'
    import swipeItem from './swipe-item'

    export default{
        data(){
            return{
                index: 0,
                time: 5000
            }
        },
        components: {
            swipe,
            swipeItem
        },
        mounted(){
            var clientWidth = document.documentElement.clientWidth;
            var clientHeight = document.documentElement.clientHeight;
            var ele = document.getElementById("caseShow");
            ele.style.height = clientHeight + 'px';
        }
    }
</script>

<style lang="scss" scoped>
  @import "../style/mixin";

  #caseShow{
    @include wh(100%, 100px);
    background-color: $fc;
  }

  header{
    position: relative;
    @include wh(100%, 100px)
  }

  .title-block{
    @include center();

    .title{
      height: 54px;
      line-height: 54px;
      color: $red;
      font-size: 34px;
      line-height: 54px;

      .small-title{
        color: $fc;
        background-color: $black;
        font-size: 18px;
      }
    }
  }

  .swipe-container{
    text-align: center;
    @include wh(100%, calc(100% - 100px));
  }

  .swipe-item-class{
    position: relative;

    .describe-background{
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 25%;
      background-color: rgba(36,61,89,0.25);
    }

    .describe{
      @include ct();
      width: 25%;
      padding: 0 60px;

      .btn{

        button{
          @include sc(20px, $fc);
          border: 2px solid $fc;
          padding: 8px 10px;
          margin-bottom: 20px;
          background-color: transparent;
        }
      }

      .describe-word{
        @include sc(14px, $fc);
      }
    }
  }

  .img-src{
    @include bis('../assets/test1.jpg');
  }
</style>



