<template>
    <div id="contact">
      <header>
        <div class="title-block">
          <div class="title">
            CONTACT
            <span class="small-title">联系我们</span>
          </div>
        </div>
      </header>

      <div id="mapAndContact" class="margin">
        <div id="map"></div>
        <div id="contactWay">
          <div id="QR-code">
            <div class="title">扫描关注微信公共平台</div>
            <div class="QR-code-img margin"><span></span></div>
          </div>
          <div id="phone-address-email">
            <div>联系电话：1312388207</div>
            <div>服务邮箱：604857875@qq.com</div>
            <div>公司地址：上海市徐汇区漕溪北路裕德路575号上海电影集团办公楼C座9层</div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>

    export default{
        data(){
            return{

            }
        },
        mounted(){
            // 百度地图API功能
            var map = new BMap.Map("map");    // 创建Map实例
            map.centerAndZoom(new BMap.Point(121.443963,31.192991), 18);  // 初始化地图,设置中心点坐标和地图级别
            map.addControl(new BMap.MapTypeControl());   //添加地图类型控件
            map.setCurrentCity("上海");          // 设置地图显示的城市 此项是必须设置的
            map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放
        }
    }
</script>

<style lang="scss" scoped>
  @import "../style/common";
  @import "../style/mixin";

  #contact{
    @include wh(100%, 550px);
    background-color: $fc;

    header{
      position: relative;
      @include wh(100%, 100px)
    }

    .title-block{
      @include center();

      .title{
        height: 54px;
        line-height: 54px;
        color: $red;
        font-size: 34px;
        line-height: 54px;

        .small-title{
          color: $fc;
          background-color: $black;
          font-size: 18px;
        }
      }
    }

    #mapAndContact{
      @include wh(62%,380px);
      background-color: $f6;

      #map{
        display: inline-block;
        @include wh(60%, 100%);
      }

      #contactWay{
        display: inline-block;
        vertical-align: bottom;
        padding: 40px 20px;
        @include wh(39%, 100%);

        #QR-code{
          text-align: center;
          margin-bottom: 24px;

          .title{
            color: $red;
            margin-bottom: 16px;
          }

          .QR-code-img{
            @include wh(160px, 160px);

            span{
              display: block;
              @include wh(160px, 160px);
              @include bis('../assets/weixin.jpg');
            }
          }
        }

        #phone-address-email{
          text-align: left;

          div{
            margin-bottom: 10px;
          }
        }
      }
    }
  }
</style>



