<template>
    <div id="products">
      <header>
        <div class="title-block">
          <div class="title">
            PRODUCTS
            <span class="small-title">主要产品</span>
          </div>
        </div>
      </header>

      <div id="showProducts">
        <div class="images">
          <div v-for="item in items" class="image-block">
            <div class="circle" :style="{ backgroundColor: item.color }"></div>
            <div class="describe">{{ item.title }}</div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default{
        data(){
            return{
                items: [
                  {
                    title: '整体家居',
                    color: 'rgba(255, 0, 0, 0.25)'
                  },
                  {
                    title: '智能家居',
                    color: 'rgba(39, 39, 39, 0.25)'
                  },
                  {
                    title: '时尚家居',
                    color: 'rgba(255, 0, 0, 0.25)'
                  },
                  {
                    title: '创意家居',
                    color: 'rgba(39, 39, 39, 0.25)'
                  }
                ]
            }
        }
    }
</script>

<style lang="scss" scoped>
  @import "../style/mixin";
  @import "../style/common";

  #products{
    @include wh(100%, 420px);
    background-color: $fc;

    header{
      position: relative;
      @include wh(100%, 100px)
    }

    .title-block{
      @include center();

      .title{
        height: 54px;
        line-height: 54px;
        color: $red;
        font-size: 34px;
        line-height: 54px;

        .small-title{
          color: $fc;
          background-color: $black;
          font-size: 18px;
        }
      }
    }
  }

  #showProducts{
    @include wh(100%, 320px);

    .images{
      @include wh(860px, 280px);
      @include fj();
      margin: 0 auto;

      .image-block{
        position: relative;
        @include wh(200px, 280px);
        background: url('../assets/test2.jpg') no-repeat 100% 100%;
      }

      .circle,.describe{
        @include wh(132px, 132px);
        @include center();
        @include sc(20px, $fc);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
      }
    }
  }
</style>



