<template>
    <div id="first-screen-container">
      <div id="transparentBlock"></div>
      <div id="describe">
        <p class="main-describe">Open a new era of dreams</p>
        <p class="remark mart16">化繁为简的线条也催生更多生活风景，让人一回到家就能感受无比的舒适与放松。</p>
        <p class="remark">打造适合一家人居住的舒适。</p>
        <div class="divide mart16"></div>
        <div id="applyProjects" class="mart16">
          <button class="button-class">申请项目</button>
        </div>
      </div>
    </div>
</template>

<script>
    export default{
        data(){
            return{

            }
        },
        mounted(){
            var clientWidth = document.documentElement.clientWidth;
            var clientHeight = document.documentElement.clientHeight;
            var ele = document.getElementById("first-screen-container");
            ele.style.height = (clientHeight - 60) + 'px';
        },
    }
</script>

<style lang="scss" scoped>
    @import '../style/mixin';

    #first-screen-container{
      position: relative;
      @include wh(100%, 10px);
      @include bis('../assets/test1.jpg');

      #transparentBlock{
        background-color: rgba(36,61,89,0.25);
        left: 18.5%;
        @include wh(38.6%, 300px);
        @include ct;
      }

      #describe{
        left: 18.5%;
        padding: 56px 29px 0;
        @include wh(38.6%, 300px);
        @include ct;

        .main-describe{
          color: $bc;
          font-size: 34px;
        }

        .remark{
          color: $bc;
          font-size: 12px;
        }
      }

      .mart16{
        margin-top: 16px;
      }

      .divide{
        background-color: $bc;
        @include wh(24px, 2px);
      }

      #applyProjects{
        @include wh(114px, 42px);
        border: $bc 2px solid;
        text-align: center;

        .button-class{
          font-size: 16px;
          line-height: 38px;
          padding: 0 16px;
          color: $bc;
          background-color: transparent;
          text-align: center;
        }
      }
    }
</style>



